import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'; 
import Header from './components/Header';
import Banner from './components/Banner';
import Services from './components/Services';
import Team from './components/Team';
import Testimonials from './components/Testimonials';
import Location from './components/Location';
import LeadForm from './components/LeadForm';
import WhatsAppButton from './components/WhatsAppButton';
import AgendeConsulta from './components/AgendeConsulta';

const App = () => {
  const [darkMode, setDarkMode] = useState(false);

  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };

  useEffect(() => {
    let timeoutId;

    const handleScroll = () => {
      // Adiciona a classe 'scroll-visible' ao body quando a tela  rolada
      document.body.classList.add('scroll-visible');

      // Limpa o timeout anterior, se o usurio continuar rolando
      clearTimeout(timeoutId);

      // Remove a classe 'scroll-visible' aps 2 segundos sem rolar
      timeoutId = setTimeout(() => {
        document.body.classList.remove('scroll-visible');
      }, 2000);
    };

    // Adiciona o listener de rolagem
    window.addEventListener('scroll', handleScroll);

    // Remove o listener e o timeout ao desmontar o componente
    return () => {
      window.removeEventListener('scroll', handleScroll);
      clearTimeout(timeoutId);
    };
  }, []);

  return (
    <Router>
      <div className={darkMode ? "dark" : ""}>
        <Helmet>
          <title>Clnica Uniderma - Cuidados Dermatolgicos</title>
          <meta name="description" content="Oferecemos os melhores cuidados dermatolgicos com profissionais experientes." />
          <meta property="og:title" content="Clnica Uniderma" />
          <meta property="og:description" content="Oferecemos os melhores cuidados dermatolgicos." />
          <meta property="og:image" content="/path/to/image.jpg" />
        </Helmet>

        <div className="bg-white dark:bg-gray-900 dark:text-white min-h-screen">
          <Header />
          <Routes>
            <Route path="/clinic-modern-web" element={
              <>
                <Banner />
                <Services />
                <Team />
                <Testimonials />
                <Location />
                <LeadForm />
              </>
            }/>
            <Route path="/agende-consulta" element={<AgendeConsulta />} /> {/* Nova rota de agendamento */}
          </Routes>
        </div>
        <WhatsAppButton />
      </div>
    </Router>
  );
};

export default App;
